/**
 * @fileOverview
 * AUI used to bundle quite a few pieces of jQuery UI in its default
 * resource bundle.
 *
 * We don't want to depend upon or bundle jquery-ui more, but there's still
 * some work to do before we could safely do that (e.g., our components
 * don't break horribly, and product teams don't either).
 *
 * For components and product teams that need it, this should be the minimum
 * amount of surface area to make the "core" of jquery-ui work. Other jquery-ui
 * widgets would depend upon this.
 */
import jQuery from 'jquery';
import 'jquery-ui/ui/core';
import 'jquery-ui/ui/widget';
export default jQuery;
